import React from "react";
import { Stack } from "@mui/material";
import { categories } from "../utils/constants";

function SideBar({ setSelectedCategory, selectedCategory }) {
  return (
    <Stack
      direction={"row"}
      sx={{ overflow: "auto", height: { sx: "auto", md: "95%" }, flexDirection: { md: "column" } }}
    >
      {categories.map((category) => (
        <button
          className="category-btn"
          onClick={() => setSelectedCategory(category.name)}
          style={{ background: category.name === selectedCategory && "#FC1503", color: "#ffffff" }}
          key={category.name}
        >
          <span
            style={{
              color: category.name === selectedCategory ? "#ffffff" : "red",
              marginRight: "15px",
            }}
          >
            {category.icon}
          </span>
          <span
            style={{
              opacity: category.name === selectedCategory ? "1" : "0.8",
              marginRight: "15px",
            }}
          >
            {category.name}
          </span>
        </button>
      ))}
    </Stack>
  );
}

export default SideBar;
